<template>
  <div class="factory-number">
    <div class="shade"></div>
    <div class="popup">
      <div class="title justify-content-between">
        <span class="fs28 colorFFF">设备编号</span>
        <img src="../assets/images/icon-close.png" @click="cancel" />
      </div>
      <div class="content">
        <div class="c-title align-item-center">检查项目：{{modelName}}</div>
        <div class="theader justify-content-between">
          <div class="w1000">序号</div>
          <div class="w4000">设备编号</div>
        </div>
        <div class="tbodys">
          <div
            class="tbody justify-content-between"
            v-for="(item, index) in list"
            :key="index"
          >
            <div class="w1000">{{ index + 1 }}</div>
            <div class="w4000">
              <el-input
                v-model="item.deviceNo"
                placeholder="请输入出厂编号"
              ></el-input>
            </div>
          </div>
        </div>
      </div>
      <div class="btns justify-content-center">
        <div class="btn sure" @click="save">确认</div>
        <div class="btn cancel" @click="cancel">取消</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "FactoryNumber",
  props: {
    id: Number,
    modelName: String
  },
  data() {
    return {
      list: [],
    };
  },
  created() {
    this.getDataList()
  },
  methods: {
    getDataList() {
      let _this = this;
      _this.axios.getEquipmentSerial({
        deviceId: this.id
      }).then(res => {
        console.log(res)
        if (res.code == 200) {
          _this.list = res.data
        }
      }).catch(function (error) {
        _this.$message.error(error.msg);
      });
    },
    save() {
      let _this = this;
      _this.axios.saveEquipmentSerial({
        updateList: _this.list
      }).then(res => {
        if (res.code == 200) {
          _this.cancel()
        }
      }).catch(function (error) {
        _this.$message.error(error.msg);
      });
    },
    cancel() {
      this.$emit("cancel");
    },
  },
};
</script>

<style scoped lang='less'>
@import url('../assets/css/public.css');
.factory-number {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  .shade {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
  }
  .popup {
    width: 666rem;
    height: 532rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    box-shadow: 0 5rem 15rem 0 rgba(0, 21, 54, 0.25);
    .title {
      width: 100%;
      height: 58rem;
      background-color: #285fe3;
      padding: 0 36rem 0 42rem;
      img {
        width: 23rem;
        height: 23rem;
        cursor: pointer;
      }
    }
    .content {
      width: 100%;
      padding: 0 45rem 0 40rem;
      .c-title {
        width: 100%;
        height: 69rem;
        line-height: 69rem;
        font-size: 24rem;
        color: #161d2e;
      }
      .theader {
        width: 100%;
        height: 40rem;
        background: #f4f5f8;
        border: 1px solid #d0d6e3;
        border-radius: 3rem;
        padding: 0 15rem;
      }
      .theader > div {
        text-align: center;
        font-size: 18rem;
        color: #505971;
      }
      .tbodys {
        width: 100%;
        height: 264rem;
        overflow-y: scroll;
      }
      /*.tbodys::-webkit-scrollbar {
        display: none;
      }
      .tbodys {
        scrollbar-width: none;
        -ms-overflow-style: none;
        overflow-x: hidden;
        overflow-y: auto;
      }*/
      .tbody {
        width: 100%;
        min-height: 60rem;
        border: 1px solid #d0d6e3;
        border-radius: 3rem;
        font-size: 18rem;
        color: #161d2e;
        margin-top: 3rem;
        padding: 9rem 15rem;
      }
      .w1000 {
        width: 10%;
        text-align: center;
      }
      .w4000 {
        width: 40%;
      }
    }
    .btns {
      width: 100%;
      position: absolute;
      bottom: 34rem;
      .btn {
        width: 130rem;
        height: 40rem;
        margin-left: 78rem;
        line-height: 40rem;
        text-align: center;
        border-radius: 3rem;
        font-size: 22rem;
        cursor: pointer;
      }
      .btn.sure {
        background: linear-gradient(0deg, #00a366 0%, #00c37a 100%);
        color: #fff;
      }
      .btn.cancel {
        border: 1px solid #316eff;
        background: linear-gradient(0deg, #d9dde8 0%, #ffffff 100%);
        color: #505971;
      }
      .btn:first-child {
        margin-left: 0;
      }
    }
  }
}

/deep/ .el-input__inner {
  height: 42rem;
  line-height: 42rem;
}
</style>


